.login {
    /* Background setting*/
    .content{
        width: 100vw;
        height: 100vh;
        background-color: #c37b10;
        position: relative;
    }

    /* Login setting*/
    .login-wrapper{
        width: 70vw;
        height: 80vh;
        background-color: rgb(255, 250, 250);
        border-radius: 40px;
        position:absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        display: flex;
        overflow: hidden;
    }

    .left-wrapper{
        border-radius: 40px;
        flex: 1;
        background-image: url('../public/login/login.jpg');
        background-size: cover;
        position: relative;
    }

    .glass{
        width: 60%;
        padding: 20px;
        color: #fff;
        position: absolute;
        top: 20%;
        left: 50%;
        transform: translate(-50%,-20%);
        background-color: rgba(237, 229, 229, 0.1);
        box-shadow: 0 8px 32px 0 rgba(43, 43, 48, 0.7);
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
        border-radius: 15px;
        border: 1px solid rgba(255, 255, 255, 0.48);
    }

    .skoog{
        margin: 15px 0 ;
    }

    .banner{
    margin: 5px 0;
    display: block;
    font-family: 100;
    }

    .right-wrapper{
        flex: 1;
        position: relative;
    }

    .form-wrapper{
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }

    .login{
        font-size: 30px;
        text-align: center;
    }


    .input-elems{
        margin: 20px 0;
    }


    .name{
        display: inline-block;
        font-weight: 600;
        font-size: 20px;
        margin: 10px 0;
    }

    .input{
        width: 100%;
        height: 50px;
        border-radius: 10px;
        border: 1px solid rgb(227, 227, 227);
        padding: 20px;
        box-sizing: border-box;
        outline: none;
        transition: 0.5s;
    }

    .input:focus{
        border: 1px solid rgb(128, 106, 196);
    }

    .forgot{
        float: right;
        font-weight: 600;
        text-align: right;
        margin: 5px 0;
        font-size: 12px;
        cursor: pointer;
    }

    .button{
        width: 100%;
        height: 50px;
        background-color: rgb(193, 197, 73);
        border: 0;
        border-radius: 10px;
        color: #fff;
        font-size: 25px;
        margin: 20px 0;
        cursor: pointer;
        box-shadow: 8px 8px 8px rgba(0,0,0,0.3), -8px -8px 8px rgba(255, 255, 255, 0.7);
    }

    .button:hover{
        background-color: rgb(136, 106, 196);
    }

    @media (max-width:1024px){
        .login-wrapper{
            padding: 20px;
        }
        .left-wrapper{
            display: none;
        }
        .right-wrapper{
            padding: 20px;
        }
    } 
}